@use "my-vars" as *;

.hero-section {
  font-family: $oswald;
  color: $white;
  background-image: linear-gradient(180deg, rgba(104, 183, 56, 0.85) 0%, rgba(104, 183, 56, 0.85) 70%, rgba(255, 255, 255, 1) 70%, rgba(255, 255, 255, 1) 100%),
    url("../img/decorative/background-promo-cat.png"),
    linear-gradient(90deg, rgba(76, 71, 76, 1) 0%, rgba(208, 185, 171, 1) 30%, rgba(208, 185, 171, 1) 70%, rgba(76, 71, 76, 1) 100%);
  background-repeat: no-repeat;
  background-size: 100%, 320px, 100%;
  padding: 23px 20px 20px 20px;
}

.hero-section__wrapper {
  min-width: 320px;
}

.hero-section__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 29px;
}

.hero-section__slogan {
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 29px;
}

.hero-section__button {
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  color: $white;
}
