@use "my-vars" as *;

.gift-order {
  width: 100%;
  min-height: 290px;
  background-image: linear-gradient(180deg, rgba(104, 183, 56, 0.85) 0%, rgba(104, 183, 56, 0.85) 100%),
    url("../img/decorative/background-gift-cat.png"),
    linear-gradient(90deg, rgba(255, 255, 255, 0.85) 0%, rgba(199, 199, 199, 0.85) 30%, rgba(199, 199, 199, 1) 70%, rgba(255, 255, 255, 1) 100%);
  background-repeat: no-repeat;
  background-size: 100%, contain, 100%;
}

.gift-order__slogan {
  font-family: $arial;
  font-weight: inherit;
  color: $white;
  padding-top: 187px;
  display: block;
}
